import {ChangeDetectorRef, Component, OnInit} from '@angular/core';
import {BaseEditView} from '../../baseEditView';
import {FormArray, FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {AuthService} from '../../../services/security/auth.service';
import {ActivatedRoute, Router} from '@angular/router';
import {ApiService} from '../../../services/api.service';
import {MatSnackBar} from '@angular/material/snack-bar';

@Component({
  selector: 'app-lottery',
  templateUrl: './lottery.component.html'
})
export class LotteryComponent extends BaseEditView implements OnInit {

  entityApi = 'lotteries';
  title: any;
  subtitle: any;
  entity: any;

  entityForm: FormGroup;

  weekDays = {
    monday: false,
    tuesday: false,
    wednesday: false,
    thursday: false,
    friday: false,
    saturday: false,
    sunday : false
  };

  loadLotteries: string;
  enumLotteryTemporality = [
    {
      value: null, text: 'Ninguna'
    },
    {
      value: 'morning', text: 'Maana'
    },
    {
      value: 'afternoon', text: 'Tarde'
    },
    {
      value: 'night', text: 'Noche'
    }
  ];

  constructor(private service: ApiService,
              private authService: AuthService,
              private route: ActivatedRoute,
              private fb: FormBuilder,
              private router: Router,
              private changeDetectorRef: ChangeDetectorRef,
              private _snackBar: MatSnackBar) {
    super();

  }

  ngOnInit(): void {

    this.title = 'Loteria';
    this.subtitle = 'Apuesta';

    // this.new();
    this.iniEntity();

    super.init(this);

    this.route.params.subscribe((params) => {

      if (params && params.lotteryId) {

        this.configFormGroup(params.lotteryId);

        super.getEntity(params.lotteryId).subscribe(() => {

          for (let day in this.weekDays) {

            this.weekDays[day] = false;
          }

          let days = this.entity.play.split(',');

          for (let day of days) {
            this.weekDays[day] = true;
            this.playDaysChange({checked: true, source: {value: day }});
          }
        });
      }
      else {
        this.configFormGroup();
      }
    });
  }

  configFormGroup = (entityId?:any) => {

    let settingName: any = {field: 'name'}

    let settingCode: any = {field: 'code'}

    if (entityId) {
      settingName.options = {
        where: {
          id: { $ne: entityId }
        }
      }

      settingCode.options = {
        where: {
          id: { $ne: entityId }
        }
      }
    }

    this.entityForm = new FormGroup({
      name: new FormControl('', [Validators.required],
        [this.service.takenValidator(this.entityApi, settingName)]),
      code: new FormControl('', [Validators.required],
        [this.service.takenValidator(this.entityApi, settingCode)]),
      checkArray: this.fb.array([], [Validators.required])
    });

  }

  iniEntity(): void {
    this.entity = {
      code: null,
      name: null,
      play: null,
      isThreeFigures: false,
      temporality: null,
      closing: null
    };
  }

  new(): void {

    for (let day in this.weekDays) {

      this.weekDays[day] = false;
    }

    if (this.entityForm) {

      const checkArray: FormArray = this.entityForm.get('checkArray') as FormArray;

      let stop =  checkArray.controls.length;

      for (let i = 0; i < stop; i++) {
        checkArray.removeAt(0);
      }
    }

    this.iniEntity();

    // this.configFormGroup();
    this.router.navigate(['/lottery']);
  }

  playDaysChange(e): void {
    const checkArray: FormArray = this.entityForm.get('checkArray') as FormArray;

    if (e.checked) {
      checkArray.push(new FormControl(e.source.value));
    } else {
      let i: number = 0;
      checkArray.controls.forEach((item: FormControl) => {
        if (item.value == e.source.value) {
          checkArray.removeAt(i);
          return;
        }
        i++;
      });
    }

    let days = '';

    checkArray.controls.forEach((item: FormControl) => {
      days += item.value + ',';
    });
    days = days.substring(0, days.length - 1);

    this.entity.play = days || null;
  }

  afterSaved(): void {
    this.loadLotteries = new Date().toString();
  }

}
