import {Component, Input, OnChanges, OnInit} from '@angular/core';
import {BaseSearchView} from '../../baseSearchView';
import {MatTableDataSource} from '@angular/material/table';
import {ApiService} from '../../../services/api.service';
import {AuthService} from '../../../services/security/auth.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-bet-alerts',
  templateUrl: './bet-alerts.component.html'
})
export class BetAlertsComponent extends BaseSearchView implements OnInit, OnChanges {

  @Input() loadChange: any;

  entityApi = 'bets';
  entityApiLotteries = 'lotteries';

  lotteries: any[] = [];
  selectedIdLotteries: string[] = [];

  settings: any = {
    showPreviewDialog: true,
    query: {
      table: 'vw_betAlerts'
    },
    options: {
      where: {
        isFourFigure: true,
        lotteryId: {$in : this.selectedIdLotteries}
      },
      order: [['accumulated', 'desc'], ['accumulatedTotal', 'desc']]
    }
  };

  title: any;
  subtitle: any;

  displayedColumns: string[] = ['lottery', 'number', 'accumulated', 'difference', 'createdDate', 'accumulatedTotal', 'actions'];
  filtersColumns: string[] = ['lottery'];
  dataSource: MatTableDataSource<any>;
  price = 7000;
  createdDate = new Date();

  constructor(private service: ApiService,
              private authService: AuthService,
              private router: Router) {
    super();
  }

  ngOnInit(): void {

    this.title = 'Apuestas Alertas';
    this.subtitle = 'Apuesta';

    super.init(this);

    let d = new Date();
    let n = d.getDay();

    let dayName = '';

    switch (n) {
      case 0:
        dayName = 'sunday';
        break;
      case 1:
        dayName = 'monday';
        break;
      case 2:
        dayName = 'tuesday';
        break;
      case 3:
        dayName = 'wednesday';
        break;
      case 4:
        dayName = 'thursday';
        break;
      case 5:
        dayName = 'friday';
        break;
      case 6:
        dayName = 'saturday';
        break;
    }

    this.service.get(this.entityApiLotteries, {options: { where:{play: { $like: '%'+ dayName +'%' }}, order:[['name', 'asc']]}})
      .subscribe((res: any) => {

        let enumLotteryTemporality = [
          {
            value: 'morning', color: 'green'
          },
          {
            value: 'afternoon', color: 'crimson'
          },
          {
            value: 'night', color: 'blue'
          },
          {
            value: null, color: 'Ninguna'
          }
        ];

        let currLotteries = res;

        for (let temp of enumLotteryTemporality) {

          let lotteriesFount = currLotteries.filter((o: any) => o.temporality == temp.value);

          for (let lottery of lotteriesFount) {
            lottery.color = temp.color;
            this.lotteries.push(lottery);
            this.selectedIdLotteries.push(lottery.id);
          }
        }

        super.onPageChange();
      });


  }

  ngOnChanges(changes: any): void {

    if (this.loadChange) {
      super.onPageChange();
    }
  }

  onEdit(row: any) {

    // this.router.navigate(['/employee', {employeeId: row.id}]);
  }

  validateAlert(row: any): boolean {

    let alert = false;

    if (row.accumulated > this.price) {
      alert = true;
    }

    return  alert;
  }

  numberOnly(event): boolean {

    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }

    return true;

  }

  downloadFile() {

    let filename = 'Numbers' + new Date();
    let text = this.getText();

    let element = document.createElement('a');
    element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(text));
    element.setAttribute('download', filename);

    element.style.display = 'none';
    document.body.appendChild(element);

    element.click();

    document.body.removeChild(element);
  }

  getText(): string {

    let text = '';

    for (let obj of this.dataSource.data) {

      if (obj.accumulated > this.price) {

        text += obj.lottery + ' ' + obj.number + ' de ' + obj.difference + ' \r\n'
      }
    }

    return text;
  }

  beforePrint(rows: any): any {

    for (let row of rows) {

      row.difference = row.accumulated - this.price;

      if (row.difference < 1) {
        row.difference = '';
      }
    }

    return rows
  }

  covered() {

    let numbers = [];

    for (let obj of this.dataSource.data) {

      if (obj.accumulated > this.price) {

        let numberFount = numbers.find((o: any) => o == obj.number);

        if (!numberFount) {
          numbers.push({number: obj.number, lotteryId:  obj.lotteryId});
        }
      }
    }

    if (numbers.length) {

      if (confirm('Ya descargo el archivo de nmeros?')) {

        var datestring = this.createdDate.getDate() + "/" + (this.createdDate.getMonth() + 1) + "/" + this.createdDate.getFullYear();

        this.service.save('details', {numbers: numbers, accumulated: this.price, createdDate: datestring})
          .subscribe((urls) => {

            this.onPageChange();

          });
      }
    }

  }

  beforePageChange(): void {

    if (this.createdDate) {

      var datestring = this.createdDate.getDate()  + "/" + (this.createdDate.getMonth()+1) + "/" + this.createdDate.getFullYear();
      // + " " +         this.createdDate.getHours() + ":" + this.createdDate.getMinutes();

      this.settings.options.where.createdDate = datestring;
    }
    else {
      delete this.settings.options.where.createdDate;
    }

    this.settings.options.where.lotteryId = this.selectedIdLotteries;
  }
}
