import {ChangeDetectorRef, Component, OnInit} from '@angular/core';
import {BaseEditView} from "../../baseEditView";
import {FormBuilder, FormControl, FormGroup, Validators} from "@angular/forms";
import {ApiService} from "../../../services/api.service";
import {AuthService} from "../../../services/security/auth.service";
import {ActivatedRoute, Router} from "@angular/router";
import {MatSnackBar} from "@angular/material/snack-bar";

@Component({
  selector: 'app-forbidden-number',
  templateUrl: './forbidden-number.component.html'
})
export class ForbiddenNumberComponent extends BaseEditView implements OnInit {

  entityApi = 'numbers';
  title: any;
  subtitle: any;
  currEntity: any;
  entities: any[] = [];

  entityForm: FormGroup;

  loadNumbers: string;

  lotteries = [];
  constructor(private service: ApiService,
              private authService: AuthService,
              private route: ActivatedRoute,
              private fb: FormBuilder,
              private router: Router,
              private changeDetectorRef: ChangeDetectorRef,
              private _snackBar: MatSnackBar) {
    super();
  }

  ngOnInit(): void {

    this.title = 'Prohibido'; //+ this.service.getUrl();
    this.subtitle = 'Apuesta';

    this.service.getSelect('lotteries', {value: 'id', text: 'name'})
      .subscribe((res: any) => {
        this.lotteries = res;

        this.lotteries.unshift({value: 'all', text: 'Todas'})
      });

    this.iniEntity();

    super.init(this);

    this.route.params.subscribe((params) => {

      if (params && params.numberId) {

        this.configFormGroup(params.numberId);

        super.getEntity(params.numberId).subscribe(() => {

        });
      }
      else {
        this.configFormGroup();
      }
    });

  }

  configFormGroup = (entityId?:any) => {

    let setting: any = {field: 'number'}

    if (entityId) {

      setting.options = {
        where: {
          id: { $ne: entityId }
        }
      }
    }

    this.entityForm = new FormGroup({
      number: new FormControl('', [Validators.required]),
      lotteryId: new FormControl('', [Validators.required])
    });
  }

  iniEntity(): void {
    this.currEntity = {
      number: null,
      isActive: true,
      lotteryId: null
    };
  }

  new(): void {

    this.iniEntity();

    this.router.navigate(['/forbidden']);
  }

  afterSaved(): void {
    this.loadNumbers = new Date().toString();
  }

  numberOnly(event: any): boolean {

    const charCode = (event.which) ? event.which : event.keyCode;
    return !(charCode > 31 && (charCode < 48 || charCode > 57));

  }

  beforeSaved(): boolean {
    let done = true;

    this.entities = [];

    if (this.currEntity.lotteryId == 'all') {

      for (let lottery of this.lotteries) {

        if (lottery.value != 'all') {

          this.entities.push(
            {
              number: this.currEntity.number,
              isActive: true,
              lotteryId: lottery.value
            }
          );
        }
      }
    }
    else {
      this.entities = [this.currEntity];
    }

    return done;
  }

}
