import {Component, Input, OnChanges, OnInit} from '@angular/core';
import {BaseSearchView} from '../../baseSearchView';
import {MatTableDataSource} from '@angular/material/table';
import {ApiService} from '../../../services/api.service';
import {AuthService} from '../../../services/security/auth.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-lotteries',
  templateUrl: './lotteries.component.html'
})
export class LotteriesComponent extends BaseSearchView implements OnInit, OnChanges {

  @Input() loadChange: any;
  @Input() showCardHeader: boolean = true;

  entityApi = 'lotteries';
  settings: any = {
    options: {
      order: [['name']]
    }
  };

  title: any;
  subtitle: any;

  displayedColumns: string[] = ['code', 'name', 'play', 'isThreeFigures', 'actions'];
  filtersColumns: string[] = ['name', 'code'];
  dataSource: MatTableDataSource<any>;
  filter: any;

  constructor(private service: ApiService,
              private authService: AuthService,
              private router: Router) {
    super();
  }

  ngOnInit(): void {
    this.title = 'Loterias';
    this.subtitle = 'Apuesta';

    // const company  = this.authService.getItem('company');
    // this.settings.options.where = {companyId: company.id};

    super.init(this);
    super.onPageChange();
  }

  ngOnChanges(changes: any): void {

    if (this.loadChange) {
      super.onPageChange();
    }
  }

  onEdit(row: any) {

    this.router.navigate(['/lottery', { lotteryId: row.id }]);
  }

}
