import {Component, Input, OnChanges, OnInit} from '@angular/core';
import {BaseSearchView} from '../../baseSearchView';
import {MatTableDataSource} from '@angular/material/table';
import {ApiService} from '../../../services/api.service';
import {AuthService} from '../../../services/security/auth.service';
import {Router} from '@angular/router';


@Component({
  selector: 'app-bet-three-figure',
  templateUrl: './bet-three-figure.component.html'
})
export class BetThreeFigureComponent extends BaseSearchView implements OnInit, OnChanges {

  @Input() loadChange: any;

  entityApi = 'bets';
  price = 70000;

  settings: any = {
    query: {
      table: 'vw_betAlerts'
    },
    options: {
      where: {
        isFourFigure: false,
        accumulatedTotal : {$gte: this.price}
      },
      order: [['accumulated', 'desc'], ['accumulatedTotal', 'desc']]
    }
  };

  title: any;
  subtitle: any;

  displayedColumns: string[] = ['lottery', 'number', 'accumulated', 'createdDate', 'accumulatedTotal', 'actions'];
  filtersColumns: string[] = ['lottery'];
  dataSource: MatTableDataSource<any>;

  createdDate = new Date();

  constructor(private service: ApiService,
              private authService: AuthService,
              private router: Router) {
    super();
  }

  ngOnInit(): void {

    this.title = 'Apuestas Alertas Tres Cifras Mayores A';
    this.subtitle = 'Apuesta';

    super.init(this);
    super.onPageChange();
  }

  ngOnChanges(changes: any): void {

    if (this.loadChange) {
      super.onPageChange();
    }
  }

  onEdit(row: any) {

    // this.router.navigate(['/employee', {employeeId: row.id}]);
  }

  validateAlert(row: any): boolean {

    let alert = false;

    if (row.accumulatedTotal >= this.price) {
      alert = true;
    }

    return  alert;
  }

  numberOnly(event): boolean {

    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }

    return true;

  }

  beforePageChange(): void {

    this.settings = {
      query: {
        table: 'vw_betAlerts'
      },
      options: {
        where: {
          isFourFigure: false,
          accumulatedTotal : {$gte: this.price}
        },
        order: [['accumulated', 'desc'], ['accumulatedTotal', 'desc']]
      }
    };

    if (this.createdDate) {

      var datestring = this.createdDate.getDate()  + "/" + (this.createdDate.getMonth()+1) + "/" + this.createdDate.getFullYear();
      // + " " +         this.createdDate.getHours() + ":" + this.createdDate.getMinutes();

      this.settings.options.where.createdDate = datestring;
    }
    else {
      delete this.settings.options.where.createdDate;
    }
  }

}
