import {Pipe, PipeTransform} from '@angular/core';
// import {GlobalizeService}    from '../services/globalize.service';

@Pipe({
    name: 'translate',
    pure: false
})
export class TranslatePipe implements PipeTransform {

  constructor(
    // private glService:GlobalizeService
  ) {
  }

  transform(value: string, args?: string[]): any {

    value = value || '';

    return (this.getMessage(value) || value);

  }

  getMessage(value) {

    let words: any = [
      {lottery: 'Lotera'},
      {firstThree: 'Tres primeras'},
      {lastThree: 'Tres ultimas'},
      {chock: 'Cua'},
      {chockNumber: 'Cua'},
      {combined: 'Combinado'},
      {number: 'Numero'},
      {price: 'Precio'},
      {employeeCode: 'Cdigo empleado'},
      {created: 'Creado'},
      {userName: 'Nombre usuario'},
      {actions: 'Acciones'},
      {accumulated: 'Acumulado'},
      {accumulatedTotal: 'Acumulado Total'}
    ];

    let word = words.find((o:any) => value in o)

    return word ? word[value] : '';
  }
}
