import {Component, Input, OnChanges, OnInit} from '@angular/core';
import {BaseSearchView} from "../../baseSearchView";
import {MatTableDataSource} from "@angular/material/table";
import {ApiService} from "../../../services/api.service";
import {AuthService} from "../../../services/security/auth.service";
import {Router} from "@angular/router";

@Component({
  selector: 'app-forbidden-numbers',
  templateUrl: './forbidden-numbers.component.html'
})
export class ForbiddenNumbersComponent extends BaseSearchView implements OnInit, OnChanges {

  @Input() loadChange: any;
  @Input() showCardHeader: boolean = true;

  entityApi = 'numbers';
  settings: any = {
    query: {
      table: 'vw_forbiddenNumbers'
    },
    options: {
      order: [['number']]
    }
  };

  title: any;
  subtitle: any;

  displayedColumns: string[] = ['number', 'isActive', 'lotteryName', 'actions'];
  filtersColumns: string[] = ['number'];
  dataSource: MatTableDataSource<any>;
  filter: any;

  constructor(private service: ApiService,
              private authService: AuthService,
              private router: Router) {
    super();
  }

  ngOnInit(): void {

    this.title = 'Numeros Prohibido';
    this.subtitle = 'Apuesta';

    super.init(this);
    super.onPageChange();
  }

  ngOnChanges(changes: any): void {

    if (this.loadChange) {
      super.onPageChange();
    }
  }

  onEdit(row: any) {

    this.router.navigate(['/forbidden', { numberId: row.id }]);
  }

}
