import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {AuthGuard} from '../../services/security/auth.guard';
import {LotteryComponent} from './lottery/lottery.component';
import {BetComponent} from './bet/bet.component';
import {BetAlertsComponent} from './bet-alerts/bet-alerts.component';
import {BetsComponent} from './bets/bets.component';
import {ForbiddenNumberComponent} from "./forbidden-number/forbidden-number.component";
import {Bet2Component} from "./bet2/bet2.component";
import {BetFourFigureComponent} from "./bet-four-figure/bet-four-figure.component";
import {BetThreeFigureComponent} from "./bet-three-figure/bet-three-figure.component";

const routes: Routes = [
  {
    path: 'betalerts',
    canActivate: [AuthGuard],
    component: BetAlertsComponent
  },
  {
    path: 'betfourfigure',
    canActivate: [AuthGuard],
    component: BetFourFigureComponent
  },
  {
    path: 'betthreefigure',
    canActivate: [AuthGuard],
    component: BetThreeFigureComponent
  },
  {
    path: 'lottery',
    canActivate: [AuthGuard],
    component: LotteryComponent
  },
  {
    path: 'bet',
    canActivate: [AuthGuard],
    component: BetComponent
  },
  {
    path: 'bet2',
    canActivate: [AuthGuard],
    component: Bet2Component
  },
  {
    path: 'bets',
    canActivate: [AuthGuard],
    component: BetsComponent
  },
  {
    path: 'forbidden',
    canActivate: [AuthGuard],
    component: ForbiddenNumberComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BetRoutingModule { }
